body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.card {
  position: absolute;
  /* inset: 0; */
  width: 2px;
  height: 2px;
  will-change: transform;
  cursor: grab;
  touch-action: none;
}

/* .card:hover {
  box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
} */

.card > div {
  will-change: transform;
  height: 100%;
  position: relative;
  margin: 0vw 0;
}

.card > div > div {
  height: 100%;
  background-size: cover;
  background-position: center center;
  margin: 0vw 0;
}

.card > div > h2 {
  position: absolute;
  top: 30%;
  left: 30%;
  margin: 0;
}

.container {
  height: 100vh;
  overflow: hidden;
  position: relative;
  /* justify-content: center; */
}

.bird {
  background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/174479/bird-cells-new.svg");
  filter: invert(34%) sepia(55%) saturate(427%) hue-rotate(141deg)
    brightness(93%) contrast(91%);
  background-size: auto 100%;
  width: 88px;
  height: 125px;
  will-change: background-position;

  animation-name: fly-cycle;
  animation-timing-function: steps(10);
  animation-iteration-count: infinite;
}

.bird-one {
  animation-duration: 1s;
  animation-delay: -0.5s;
}
/* .bird-two {
  animation-duration: 0.9;
  animation-delay: -0.75s;
} */
.bird-three {
  animation-duration: 1.25s;
  animation-delay: -0.25s;
}
.bird-four {
  animation-duration: 1.1s;
  animation-delay: -0.5s;
}

.bird-container {
  position: absolute;
  top: 10%;
  left: -3%;
  transform: scale(0) translateX(-10vw);
  will-change: transform;

  animation-name: fly-right-one;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.bird-container-one {
  animation-duration: 15s;
  animation-delay: 0;
}

/* .bird-container-two {
  animation-duration: 16s;
  animation-delay: 1s;
} */

.bird-container-three {
  animation-duration: 14.6s;
  animation-delay: 9.5s;
}

.bird-container-four {
  animation-duration: 16s;
  animation-delay: 10.25s;
}
/* @keyframes fly-cycle {
  100%{
      background-position: -3600px 0;
  }
} */
@keyframes fly-cycle {
  100% {
    background-position: -900px 0;
  }
}

@keyframes fly-right-one {
  0% {
    transform: translateY(50vh) scale(0.3) translateX(-10vw);
  }

  10% {
    transform: translateY(30vh) translateX(10vw) scale(0.4);
  }

  20% {
    transform: translateY(10vh) translateX(30vw) scale(0.5);
  }

  30% {
    transform: translateY(0vh) translateX(50vw) scale(0.6);
  }

  40% {
    transform: translateY(-10vh) translateX(70vw) scale(0.6);
  }

  50% {
    transform: translateY(-20vh) translateX(90vw) scale(0.6);
  }

  60% {
    transform: translateY(-40vh) translateX(110vw) scale(0.6);
  }

  70% {
    transform: translateY(-60vh) translateX(110vw) scale(0.6);
  }

  80% {
    transform: translateY(-80vh) translateX(110vw) scale(0.6);
  }

  90% {
    transform: translateY(-100vh) translateX(110vw) scale(0.6);
  }

  100% {
    transform: translateY(-120vh) translateX(110vw) scale(0.6);
  }
}

@keyframes fly-right-two {
  0% {
    transform: translateY(-2vh) translateX(-10vw) scale(0.5);
  }

  10% {
    transform: translateY(0vh) translateX(10vw) scale(0.4);
  }

  20% {
    transform: translateY(-4vh) translateX(30vw) scale(0.6);
  }

  30% {
    transform: translateY(1vh) translateX(50vw) scale(0.45);
  }

  40% {
    transform: translateY(-2.5vh) translateX(70vw) scale(0.5);
  }

  50% {
    transform: translateY(0vh) translateX(90vw) scale(0.45);
  }

  51% {
    transform: translateY(0vh) translateX(110vw) scale(0.45);
  }

  100% {
    transform: translateY(0vh) translateX(110vw) scale(0.45);
  }
}

#movingImage {
  width: 300px;
  height: 300px;
  position: fixed;
  top: 850px;
  animation: moveRight 5s linear infinite; /* Adjust the duration and timing function as needed */
}

@keyframes moveRight {
  0% {
    left: 0;
  }
  100% {
    left: calc(100vw - 650px);
  } /* Adjust the final position */
}

.cloud {
  background-size: cover;
  background-repeat: no-repeat;
  height: 35%;
  position: absolute;
  inset: 0;
  animation: animate 60s linear infinite;
}

@keyframes animate {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -60vw 0;
  }
}
