nav {
  /* width: 100%;
  height: 100vh; */
  /* width: 100%;
  height: 100vh; */
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  /* background: #40403E; */
}
span {
  background: #6a0026;
  color: #fffff0;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font: 36px fontawesome;
  cursor: pointer;
  position: relative;
  z-index: 1;
}
span:hover {
  box-shadow: 0px 0px 30px 3px rgba(0, 0, 0, 0.5);
}
.menu-item {
  text-decoration: none;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  font: 42px fontawesome;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 1s ease, background 0.5s ease, color 0.5s ease;
  position: relative;
  index: -5;
}
.menu-item:hover {
  box-shadow: 0px 0px 30px 3px rgba(0, 0, 0, 0.5);
}
#menu-icon {
  transition: transform 1s ease;
}
#menu-icon.active {
  transition: all 1s ease;
  transition-delay: 1.3s;
}
