body {
  font-family: 'Avenir';
  font-weight: 800;
  text-transform: uppercase;
}

#scroll-container-district {
  overflow: hidden;
  /* max-width: 100%; */
  margin: auto;
  /* height: 46vh; */
  bottom: 415px;
  position: absolute;
  min-height: 500px;
  right: 1000px;
  bottom: 20%;
  width: 500px;
  background-image: url("../../public/asset/arc/pink-right.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.wrap-container-dis {
  position: relative;
  max-height: 413px;
  width: 500px;
  max-width: 100%;
  margin: auto;
  overflow-x: hidden;
  overflow-y: auto;
  padding-right: 20px;
  mask: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 10%, rgba(0,0,0,1) 90%, rgba(0,0,0,0) 100%), linear-gradient(to top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 10%, rgba(0,0,0,1) 90%, rgba(0,0,0,0) 100%);
}

.wrap-container-dis ul {
  list-style: none;
  position: relative;
  transition: transform .3s;
  top: 50px;
}

.wrap-container-dis ul, 
.wrap-container-dis ul li {
  margin: 0;
  padding: 0;
  text-align: center;
}

.wrap-container-dis ul li {
  height: 90px;
  /* line-height: 70px; */
  transition: transform .3s;
  overflow: hidden;
  align-items: center;
  display: flex;
  justify-content: center;
 
}

.wrap-container-dis ul li:last-of-type {
  padding-bottom: 50px;
}

.wrap-container-dis ul li.active .item {
  transform: scale(1.9);
}

.wrap-container-dis ul li .item {
  position: relative;
  transition: transform 200ms;
  display: inline-block;
  align-items: center;
}
.selected-cap2{
background-color: #317CA6;
}



.curved-div-dis {
  /* background: #FEC5AA; */
  /* clip-path: polygon(1% 8%, 104% 0, 100% 0%, 100% 100%, -2% 90%, 0% 90%, 0 87.9%); */
  transform: scaleX(-1);
  
}

.active{
  transform: scale(1.2);
}