body {
    font-family: 'Avenir';
    font-weight: 800;
    text-transform: uppercase;
  }
  
  #scroll-container-sub {
    overflow: hidden;
    width: 500px;
    margin: auto;
    height: 46vh;
    top: -40vh;
    position: relative;
    min-height: 200px;
    left: 220%;
    background-image: url("../../public/asset/arc/pink-right.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .wrap-container {
    position: relative;
    height: 400px;
    width: 450px;
    max-width: 100%;
    margin: auto;
    overflow-x: hidden;
    overflow-y: auto;
    padding-right: 20px; /* hides scrollbar */
    /* mask: url(#masking);
    -webkit-mask-image: -webkit-gradient(linear, left bottom, left top, 
     color-stop(0.5,  rgba(0,0,0,1)),
     color-stop(1,  rgba(0,0,0,0))); */
  }
  
  .wrap-container ul {
    list-style: none;
    position: relative;
    transition: transform .3s;
    top: 50px;
    margin: 20px 0 20px 0 !important;
  }
  
  .wrap-container ul, 
  .wrap-container ul li {
    margin: 0;
    padding: 0;
    text-align: center;
    margin: 20px 0 20px 0;
  }
  
  .wrap-container ul li {
    height: 80px;
    /* line-height: 70px; */
    transition: transform .3s;
    overflow: hidden;
    align-items: center;
    display: flex;
    justify-content: center;
    margin: 5px 0 5px 0;
  }
  
  .wrap-container ul li:last-of-type {
    padding-bottom: 50px;
  }
  
  .wrap-container ul li.active .item {
    transform: scale(1.9);
  }
  
  .wrap-container ul li .item {
    position: relative;
    transition: transform 200ms;
    display: inline-block;
    align-items: center;
  }
  .selected-cap2{
    /* transform: scale(1.5); */
    fill: linear-gradient(90deg, #317CA6 2.01%, rgba(49, 124, 166, 0.00) 100%);
  }

