#scroll-container-state {
  overflow: hidden;
  width: 500px;
  margin: auto;
  /* top: 34vh; */
  position: relative;
  min-height: 200px;
  right: 122%;
  height: 400px !important;
  /* background-color: #317ca6; */
}

.wrap-container {
  position: relative;
  max-height: 400px;
  width: 100%;
  max-width: 100%;
  margin: auto;
  overflow-x: hidden;
  overflow-y: auto;
  padding-right: 20px;
  mask: url(#masking);
  -webkit-mask-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    color-stop(0.5, rgba(0, 0, 0, 1)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
}

.wrap-container ul {
  list-style: none;
  position: relative;
  transition: transform 0.3s;
  top: 50px;
}

.wrap-container ul,
.wrap-container ul li {
  margin: 0;
  padding: 0;
  text-align: center;
}

.wrap-container ul li {
  height: 90px;
  /* line-height: 70px; */
  transition: transform 0.3s;
  overflow: hidden;
}

.wrap-container ul li:last-of-type {
  padding-bottom: 50px;
}

.wrap-container ul li.active .item {
  transform: scale(1.9);
}

.wrap-container ul li .item {
  position: relative;
  transition: transform 200ms;
  display: inline-block;
}

.capsule {
  border-radius: 56.43px;
  border: 3px solid #6a0026;
  height: 70px;
  background: #fec5aa;
}

.selected-capsule1 {
  border-radius: 56.43px;
  border: 3px solid #6a0026;
  height: 70px;
  background: linear-gradient(90deg, #6a0026 2.01%, rgba(49, 124, 166, 0) 100%);
}
