
  #scroll-container-indicators {
    overflow: hidden;
    max-width: 100%;
    margin: auto;
    height: 50vh;
    /* top: 25vh; */
    position: relative;
    min-height: 200px;
  }
  
  .wrap-container-indicators {
    position: relative;
    /* max-height: 200px; */
    width: 600px;
    max-width: 100%;
    margin: auto;
    overflow: auto;
    -webkit-overflow-scrolling: touch !important;
    padding-right: 20px; /* hides scrollbar */
    mask: url(#masking);
    -webkit-mask-image: -webkit-gradient(linear, left bottom, left top, 
     color-stop(0.5,  rgba(0,0,0,1)),
     color-stop(1,  rgba(0,0,0,0)));
  }
  
  .wrap-container-indicators ul {
    list-style: none;
    position: relative;
    transition: transform .3s;
    top: 50px;
  }
  
  .wrap-container-indicators ul, 
  .wrap-container-indicators ul li {
    margin: 0;
    padding: 0;
    text-align: center;
  }
  
  .wrap-container-indicators ul li {
    /* height: 50px; */
    line-height: 50px;
    transition: transform .3s;
    overflow: hidden;
  }
  
  .wrap-container-indicators ul li:last-of-type {
    padding-bottom: 50px;
  }
  
  .wrap-container-indicators ul li.active .item-indicators {
    transform: scale(1.9);
  }
  
  .wrap-container-indicators ul li .item-indicators {
    position: relative;
    transition: transform 200ms;
    display: inline-block;
    line-height: normal;
  }
  

  .indicators-list{
    width: 400px !important;
    height: 300px !important;
    overflow: scroll !important;
    mask: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 10%, rgba(0,0,0,1) 90%, rgba(0,0,0,0) 100%), linear-gradient(to top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 10%, rgba(0,0,0,1) 90%, rgba(0,0,0,0) 100%);
}

.indicator-selected{
    background-color: rgb(118, 118, 255);
}