body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  /* background-image: url("../../public/asset/bg/blush.jpg"); */
  background-position: center;
  background-repeat: no-repeat;
  margin: 0px;
  padding: 0px;
}

.label {
  text-align: center;
  color: #084160;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
}

body input,
body h1,
body h2,
body h3,
body a {
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  margin: 0px;
  padding: 0px;
}
#app-container {
  position: absolute;
  left: 50%;
}
#app {
  /* height: 100vh; */
  height: 300px;
  overflow: hidden;
}
#app #menu-toggle {
  background-color: transparent;
  /* left: -9vh; */
  top: 37vh;
  position: absolute;
  z-index: 3;
}

#app #menu-toggle-indicator {
  background-color: transparent;
  /* left: 1300px; */
  /* bottom: -8vh; */
  /* position: absolute; */
  z-index: 3;
  width: 400px;
  height: 200px;
  text-align: center;
  align-items: center;
}

#app #menu-toggle i {
  font-size: 2vh;
  height: 3vh;
  line-height: 3vh;
  text-align: center;
  width: 3vh;
}
#app #menu {
  height: 100vh;
  opacity: 0;
  position: absolute;
  transform: translateX(-50%);
  transition: opacity 250ms, transform 250ms;
  transition-delay: 300ms;
  width: 40vh;
}
#app #menu.toggled {
  opacity: 1;
  transform: translateX(0%);
  transition-delay: 0ms;
}
#app #menu.toggled #right-quick-options .menu-quick-option {
  opacity: 1;
  transform: translateX(0%);
}
#app #menu.toggled #right-quick-options .menu-quick-option:first-of-type,
#app #menu.toggled #right-quick-options .menu-quick-option:last-of-type {
  transform: translateX(-50%);
}
#app #menu.toggled #right-full-options .menu-full-option {
  opacity: 1;
  transform: translateX(-0.5vh);
}
#app #menu.toggled #right-full-options .menu-full-option:first-of-type,
#app #menu.toggled #right-full-options .menu-full-option:last-of-type {
  transform: translateX(-4vh);
}
#app #menu.toggled #right-full-options .menu-full-option:nth-of-type(2),
#app #menu.toggled #right-full-options .menu-full-option:nth-of-type(4) {
  transform: translateX(-1vh);
}
#app #menu.toggled #menu-background-wrapper #menu-background:before {
  transform: translate(-50%, -50%);
  transition-delay: 200ms;
}
#app #menu #menu-background-wrapper {
  height: 100vh;
  left: 0px;
  position: absolute;
  top: 0px;
  width: 40vh;
  z-index: 1;
}
#app #menu #menu-background-wrapper #menu-background {
  /* background-color: #64B5F6; */
  /* border-bottom-right-radius: 100%;
    border-top-right-radius: 100%; */
  /* box-shadow: 0px 0px 20px 4px rgba(0, 0, 0, 0.15); */
  /* height: 200vh;
    position: absolute;
    right: 0px;
    top: -50vh;
    width: 200vh; */
}
#app #menu #menu-background-wrapper #menu-background:before {
  height: 120%;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-100%, -50%);
  transition: transform 250ms;
  width: 120%;
  z-index: -1;
}
#menu-profile-image {
  object-fit: cover;
  position: absolute;
  top: 30vh;
  left: -20vh;
  /* transform: translate(-50%, -50%); */
  /* width: 40vh; */
  z-index: 2;
}
#app #menu #right-quick-options {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 60vh;
  justify-content: space-around;
  position: absolute;
  top: 50%;
  transform: translate(100%, -50%);
  width: 165px; /*20vh*/
  z-index: 3;
}
#app #menu #right-quick-options .menu-quick-option {
  align-items: center;
  background-color: white;
  border: none;
  border-radius: 8vh;
  box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  display: flex;
  min-height: 5vh;
  justify-content: space-around;
  opacity: 0;
  padding: 20px;
  text-decoration: none;
  transform: translateX(-30%) scale(0.25);
  transition: opacity 150ms, transform 150ms;
  width: 25vh;
}
/* #app #menu #right-quick-options .menu-quick-option:hover {
    background-color: #f0f0f0;
  } */
#app #menu #right-quick-options .menu-quick-option:hover .tooltip {
  opacity: 1;
  transform: translateX(100%);
}
#app #menu #right-quick-options .menu-quick-option:first-of-type {
  transform: translate(-80%, 30%) scale(0.5);
}
#app #menu #right-quick-options .menu-quick-option:last-of-type {
  transform: translate(-80%, -30%) scale(0.5);
}
#app #menu #right-quick-options .menu-quick-option i {
  color: #64b5f6;
  font-size: 2vh;
  height: 3vh;
  line-height: 3vh;
  text-align: center;
  width: 3vh;
}
#app #menu #right-quick-options .menu-quick-option .tooltip {
  background-color: #1e1e1e;
  border-radius: 0.5vh;
  box-shadow: 0px 0px 1vh 0.25vh rgba(0, 0, 0, 0.08);
  color: white;
  font-size: 1em;
  opacity: 0;
  padding: 1vh;
  pointer-events: none;
  position: absolute;
  right: -1vh;
  transform: translateX(90%);
  transition: opacity 250ms, transform 250ms;
}
#app #menu #right-full-options {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  height: 60vh;
  justify-content: space-around;
  left: 42vh;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  width: 200px;
}
#app #menu #right-full-options .menu-full-option {
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 0.5vh;
  cursor: pointer;
  display: flex;
  gap: 1vh;
  opacity: 0;
  padding: 1vh;
  text-decoration: none;
  transform: translateX(-2vh);
  transition: opacity 150ms, transform 150ms;
}
/* #app #menu #right-full-options .menu-full-option:hover {
    background-color: rgba(255, 255, 255, 0.05);
  } */
#app #menu #right-full-options .menu-full-option:first-of-type,
#app #menu #right-full-options .menu-full-option:last-of-type {
  transform: translateX(-6vh);
}
#app #menu #right-full-options .menu-full-option:nth-of-type(2),
#app #menu #right-full-options .menu-full-option:nth-of-type(4),
#app #menu #right-full-options .menu-full-option:nth-of-type(6) {
  transform: translateX(-4vh);
}
#app #menu #right-full-options .menu-full-option i,
#app #menu #right-full-options .menu-full-option h3 {
  color: #084160;
  font-family: Inter;
  font-weight: 600;
  line-height: normal;
  font-size: 1.5vh;
  height: 2vh;
  line-height: 2vh;
}
#left-options-container {
  position: absolute;
  top: 50%;
  left: -50vw; /* Adjust this value as needed */
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the left */
  width: 20vh;
  z-index: 3;
}
#left-options-container .menu-quick-option,
#left-options-container .menu-full-option {
  transform-origin: 100% 50%; /* Set the transformation origin to the right */
}
#app #menu #left-quick-options {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 60vh;
  justify-content: space-around;
  position: absolute;
  top: 50%;
  transform: translate(100%, -50%);
  width: 380px; /*30vh*/
  z-index: 3;
}
#app #menu #left-full-options {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  height: 60vh;
  justify-content: space-around;
  left: -6vh;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 20vh;
  z-index: 2;
}
#app #menu #left-quick-options .menu-quick-option {
  align-items: center;
  background-color: white;
  border: none;
  border-radius: 8vh;
  box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  display: flex;
  min-height: 5vh;
  justify-content: space-around;
  opacity: 1;
  padding: 20px;
  text-decoration: none;
  transform: translateX(-30%) scale(1);
  transition: opacity 150ms, transform 150ms;
  width: 25vh;
}
/* #app #menu #left-quick-options .menu-quick-option:hover {
    background-color: #f0f0f0;
  } */
#app #menu #left-quick-options .menu-quick-option:hover .tooltip {
  opacity: 1;
  transform: translateX(100%);
}
#app #menu #left-quick-options .menu-quick-option:first-of-type {
  transform: translate(30%, 0%) scale(1);
}
#app #menu #left-quick-options .menu-quick-option:last-of-type {
  transform: translate(30%, 0%) scale(1);
}
/* #app #menu #left-full-options .menu-full-option:hover {
    background-color: rgba(255, 255, 255, 0.05);
  } */
#app #menu #left-full-options .menu-full-option:first-of-type,
#app #menu #left-full-options .menu-full-option:last-of-type {
  transform: translateX(8vh);
}
#app #menu #left-full-options .menu-full-option:nth-of-type(2),
#app #menu #left-full-options .menu-full-option:nth-of-type(4) {
  transform: translateX(4vh);
}
#app #menu #left-full-options .menu-full-option i,
#app #menu #left-full-options .menu-full-option h3 {
  color: rgb(255, 255, 255);
  font-size: 1.5vh;
  height: 2vh;
  line-height: 2vh;
}
#right-options-container {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the left */
  width: 20vh;
  z-index: 3;
}
.logo_div {
  background-image: url("../../public/asset/logo/bg_logo.svg");
  padding: 5px;
  border-radius: 50%;
  background-size: cover;
}
.logo_sector {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}
