body {
  font-family: "Avenir";
  font-weight: 800;
  text-transform: uppercase;
}

#scroll-container {
  overflow: hidden;
  /* max-width: 100%; */
  width: 500px;
  margin: auto;
  /* height: 46vh; */
  top: 37vh;
  position: relative;
  min-height: 200px;
  left: 96%;
  height: 400px !important;
  /* background-color: #317ca6; */
}

.wrap-container {
  position: relative;
  max-height: 400px;
  width: 100%;
  max-width: 100%;
  margin: auto;
  overflow-x: hidden;
  overflow-y: auto;
  padding-right: 20px; /* hides scrollbar */
  /* mask: url(#masking);
    -webkit-mask-image: -webkit-gradient(linear, left bottom, left top, 
     color-stop(0.5,  rgba(0,0,0,1)),
     color-stop(1,  rgba(0,0,0,0))); */

  mask: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 1) 10%,
      rgba(0, 0, 0, 1) 90%,
      rgba(0, 0, 0, 0) 100%
    ),
    linear-gradient(
      to top,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 1) 10%,
      rgba(0, 0, 0, 1) 90%,
      rgba(0, 0, 0, 0) 100%
    );
  /* -webkit-mask-image: 
     -webkit-gradient(linear, left bottom, left top, color-stop(0.1, rgba(0,0,0,0)), color-stop(0.1, rgba(0,0,0,1)), color-stop(0.9, rgba(0,0,0,1)), color-stop(0.9, rgba(0,0,0,0))); */
}

.wrap-container ul {
  list-style: none;
  position: relative;
  transition: transform 0.3s;
  top: 50px;
}

.wrap-container ul,
.wrap-container ul li {
  margin: 0;
  padding: 0;
  text-align: center;
  margin: 20px 0 20px 0;
}

.wrap-container ul li {
  height: 80px;
  /* line-height: 70px; */
  transition: transform 0.3s;
  overflow: hidden;
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 5px 0 5px 0;
}

.wrap-container ul li:last-of-type {
  padding-bottom: 50px;
}

.wrap-container ul li.active .item {
  transform: scale(1.9);
}

.wrap-container ul li .item {
  position: relative;
  transition: transform 200ms;
  display: inline-block;
}

.capsule {
  border-radius: 56.43px;
  border: 3px solid #6a0026;
  height: 60px;
}

.selected-capsule {
  border-radius: 56.43px;
  border: 3px solid #6a0026;
  height: 60px;
  /* background: linear-gradient(90deg, #6a0026 2.01%, rgba(49, 124, 166, 0) 100%); */
  color: #fff;
}

.logo-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #a3043d;
  border-radius: 50%;
  height: 60px;
  width: 60px;
}
.logo {
  height: 60px;
  width: 60px !important;
  border-radius: 50%;
}
